.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 1400px;
    margin-top: 80px;
}

.container__hunter{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.container__hunter__container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: 30px;
}

.container__columns{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.item{
    position: relative;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: rgba(0,0,0,0.3);
    border: 5px solid rgba(0,0,0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    font-size: 0;
}

.item:hover{
    background-color: rgba(0,0,0,0.08);
}

.item:disabled:hover{
    color: black;
}
.item:disabled{
    background-color: rgba(0,0,0,0.8)
}

.item.active{
    background-color: transparent;
}
.item:hover::after{
    content: "Стоимость навыка: "attr(data-cost) "    Описание: " attr(data-info);
    position: absolute;
    padding: 15px;
    top: 10px;
    left: 60px;
    width: 200px;
    height: 300px;
    border: 1px solid rgba(0,0,0,0.5);
    background-color: white;
    border-radius: 10px;
    z-index: 999;
    font-size: 16px;
}

.tree_from_item{
    position: absolute;
    border: 2px solid rgba(0,0,0,0.5);
    height: 7px; 
    width: 1px;
    bottom: -11px;
}
.branch_from_tree_right{
    position: absolute; 
    height: 1px;
    width: 177px;
    bottom: -2px;
    left: 3px;
    border: 2px solid rgba(0,0,0,0.5);
}
.branch_from_right_branch{
    position: absolute;
    border: 2px solid rgba(0,0,0,0.5);
    width: 1px;
    top: 2px;
    right: -2px;
    height: 14px;
}

.branch_from_tree_left{
    position: absolute;
    border: 2px solid rgba(0,0,0,0.5);
    height: 1px; 
    width: 177px;
    bottom: -2px; 
    right: 3px;
}

.branch_from_left_branch{
    position: absolute;
    border: 2px solid rgba(0,0,0,0.5);
    width: 1px;
    top: 2px;
    left: -2px;
    height: 14px;
}

.branch__vertical{
    position: absolute;
    width: 1px;
    border: 2px solid rgba(0,0,0,0.5);
    bottom: -27px;
    height: 20px;
}

.branch__horizontal_right{
    position: absolute;
    height: 1px;
    width: 30px;
    right: -37px;
    border: 2px solid rgba(0,0,0,0.5);
}

.branch__horizontal_left{
    position: absolute;
    height: 1px;
    width: 30px;
    left: -37px;
    border: 2px solid rgba(0,0,0,0.5);
}

.branch__vertical_long{
    position: absolute;
    width: 1px;
    border: 2px solid rgba(0,0,0,0.5);
    bottom: -107px;
    height: 100px;
}

.popup__container{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.9);
    z-index: 999;
}

.popup__container.active{
    top: -3000px;
}

.result__container{
    width: 300px; 
    max-height: 100vh; 
    min-height: 100vh; 
    overflow-y: scroll;
    border: 2px solid rgba(0,0,0,0.5);
    border-radius: 10px;
    position: absolute;
    left: 10px;
    top: 200px;
}

@media(max-width: 1270px){
    .container{
        margin-top: 500px;
    }
    .result__container{
        left: 300px;
        top: 80px;
        width: 500px;
        max-height: 400px; 
        min-height: 400px;
    }
}